import React from 'react';
import PropTypes from 'prop-types';

const SettingsDisclaimer = ({ onDismiss, theme }) => {
  return (
    <div className={`settings-disclaimer tile ${theme}`}>
      <span className="disclaimer-contents">{ t('PersistentSettingsDisclaimer') }</span>
      <div
        className="disclaimer-dismiss"
        onClick={onDismiss}
        role="button"
      />
    </div>
  );
};

SettingsDisclaimer.propTypes = {
  onDismiss: PropTypes.func,
};

export default SettingsDisclaimer;
